import './BtnBlock.scss';
import {Link} from 'react-router-dom'

const BtnBlock = () => {
    return(
        <div className="btnBlock container">
            <div className="btnBlock-column">
                <Link></Link>
                <Link></Link>
                <Link> / </Link>
                <Link> / </Link>
                <Link></Link>
            </div>
            
            <div className="btnBlock-column">
                <Link>   </Link>
                <Link>  </Link>
                <Link>  </Link>
                <Link> </Link>
                <Link> </Link>
            </div>

            <div className="btnBlock-column">
                <Link>  </Link>
                <Link> </Link>
                <Link></Link>
                <Link></Link>
                <Link> / </Link>
            </div>

            <div className="btnBlock-column">
                <Link></Link>
                <Link></Link>
                <Link> / </Link>
                <Link> / </Link>
                <Link></Link>
            </div>
        </div>
    )
}

export default BtnBlock;